export const pullListingsPaged = (skip: number, take:number, filters:Filter[]) => {
  
  let filterJSON = "[";
  if(filters && filters.length > 0){
    filters.forEach((filter:Filter, index:number) => {
        filterJSON += "{type:\"" + filter.type + "\", value:\"" + filter.value + "\"}";
        if(index + 1 < filters.length){
          filterJSON += ",";
        }
    });
  }
  filterJSON += "]";
  
  const regionid = "00000000-0000-0000-0000-000000000001";// TODO: RegionID will be available when routing story is complete;
  const query = `{
    listings (skip: ${skip}, take: ${take}, filterOptions: ${filterJSON}, regionID: \"${regionid}\") {
      id
      miqId
      state
      propertyName
      propertyRecordName
      configId
      street
      street2
      city
      stateOrProvince
      postalCode
      propertyType
      listingType
      published
      spacesCount
      externalPublishUrl
      isDeleted 
      photos {
        active
        displayText
        primary
        url
      }
      specifications {
        maxPrice
        maxSpace
        measure
        minPrice
        minSpace
        totalSpace
        salePrice
      }
      contacts {
        firstName
        lastName
        avatar
      }
      spaces {
        name {
          cultureCode
          text
        }
      }
      users {
        id
      }
      listingAssignment {
        assignedBy
        assignmentFlag
        assignedDate
      }
    }
  }`;
  return query; 
};

export const findMiQProperties = (term: string, countryCode: string) => {
  const query = `
  {
    searchEdpProperties(keyword:"${term}",country:"${countryCode}") 
    {
      id
      name
      street1
      street2
      city
      stateProvince
      postalCode
      propertyType
    }
  }
  `;
  return query;
}

